<template>
  <div>
    <b-row>
      <b-col cols="12" xl="2">
        <b-button variant="primary" class="px-16 lable-btnAny">{{
          $t("SELECTCONFIRMATION")
        }}</b-button>
      </b-col>
      <b-col cols="12" xl="10" class="title-fw title-fs mb-2 mobileContent">
        {{ $t("THATISALLTEXT") }}
      </b-col>
    </b-row>
    <br />
    <b-row
      v-for="question_list in subsidyQuestionAnswers"
      :key="question_list.id"
    >
      <b-col cols="12" xl="3"></b-col>
      <b-col cols="3" xl="1">
        <label class="highLightHeaderLabel">{{
          $t("QUESTION") + question_list.question.question_no
        }}</label>
      </b-col>
      <b-col cols="8" xl="5">
        <label class="highLightHeaderLabel">{{
          question_list.question.question_contents
        }}</label>
      </b-col>
      <b-col cols="3" xl="4"></b-col>
      <b-col cols="8" xl="5"
        ><label class="highLightHeaderLabel">{{
          question_list.answer.questionContents
        }}</label></b-col
      >
    </b-row>
    <br />
    <b-row>
      <b-col cols="12" xl="3"></b-col>
      <b-col cols="6" xl="2">
        <b-button @click="goToJudgementResult" variant="primary" class="mt-2">{{
          $t("RECOMMENCEBTN")
        }}</b-button>
      </b-col>
      <b-col cols="5" xl="2">
        <b-button @click="goToBack" variant="primary" class="mt-2">{{
          $t("SUBSIDYGRANTJUDGEMENTRESULTBTN")
        }}</b-button>
      </b-col>
    </b-row>
    <br /><br /><br />
  </div>
</template>

<script>
import { mapGetters } from "vuex";
export default {
  name: "ContentConfirmation",
  components: {},
  data() {
    return {};
  },

  computed: {
    ...mapGetters({
      subsidyQuestionAnswers: "getSessionQA",
    }),
  },
  mounted() {},
  created() {},
  methods: {
    /**Function to go back for question and answer screen and listing from questions from starting */
    goToBack() {
      this.$router.push({ name: "subsidyGrantJudgement_chart" });
    },

    /**Analyse judgement point of each answer, update points to the store and send to judgement result screen */
    goToJudgementResult() {
      let judgementPoint = 0;
      this.subsidyQuestionAnswers.forEach((item) => {
        judgementPoint = judgementPoint + parseInt(item.answer.judgePoint);
      });
      this.$store.dispatch("updateJudgementPoint", {
        judgePoint: judgementPoint,
      });
      this.$router.push({
        name: "subsidyGrantJudgement_result",
      });
    },
  },
};
</script>

<style lang="scss" scoped>
.highLightHeaderLabel {
  font-weight: 700 !important;
  padding-top: 5px;
}
@media (min-width: 991px) {
  .mobileContent{
    padding-left: 0px !important;
  }
}
</style>